import React, { useEffect, useState } from 'react';
import { Calendar, momentLocalizer } from 'react-big-calendar';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import moment from 'moment';
import 'moment/locale/es';
import { Navbar } from '../ui/Navbar'
import { messages } from '../../helpers/calendar-messages-es';
import { CalendarEvent } from './CalendarEvent';
import { CalendarModal } from './CalendarModal';
import { uiOpenModal } from '../../actions/ui';
import {useDispatch, useSelector} from 'react-redux'
import { eventClearActiveEvent, eventSetActive, eventStartLoading } from '../../actions/event';
import { AddNewFab } from '../ui/AddNewFab';
import { DeletedEventFab } from '../ui/DeletedEventFab';

moment.locale('es');
/* Cambiar idioma a español */
const localizer = momentLocalizer(moment);

/* const events=[{
    title: 'Cumpleaños del jefe',
    start: moment().toDate(),
    end:moment().add(2, 'hours').toDate(),
    bgcolor:'#fafafa',
    notes:'Comprar el pastel',
    user:{
      _id:'123',
      name:'Junior',
    }
}] */


export const CalendarScreen = () => {

  const dispatch = useDispatch();
  const {events, activeEvent} = useSelector( state => state.calendar );
  const {uid} = useSelector( state => state.auth );



  const [lastView, setLastView] = useState(localStorage.getItem('lastView') || 'month');

  useEffect(() => {
    dispatch(eventStartLoading());
  }, [dispatch])
  


  const onDoubleClick=(e)=>{
    console.log(e);
    console.log(`Abrir Modal`);
    dispatch(uiOpenModal());
  }


 const onSelectEvent=(e)=>{
    console.log(e);
    dispatch(eventSetActive(e));
    /* dispatch(uiOpenModal()); */
  }

  /* veo en que parte estoy dia, mesa,semana, month */
const onViewChange=(e)=>{
  localStorage.setItem('lastView',e); //vista actual
  setLastView(e)
}


const onSelectSlot=(e)=>{
  console.log(e)
  dispatch(eventClearActiveEvent(e)); /* Pone el evento activo en null */
}



/* Pasa estilos al componente calendar */
  const eventStyleGetter=(event, start, end, isSelected )=>{
   /*  console.log(event, start, end, isSelected); */
    /* console.log(event); */
    const style={
      backgroundColor:(uid===event.user._id)?'#367CF7':'#465660', //sino es el usuario el que creó el evento pone otro color 
      borderRadius: '0px',
      opacity:0.8,
      display:'block',
      color:'white'
    }

    return{
      style
    }

  }




  return (
    <div className="calendar-screen">
       <Navbar />

       <Calendar
      localizer={localizer}
      events={events}
      startAccessor="start"
      endAccessor="end"
      messages={messages}
      eventPropGetter={eventStyleGetter}
      onDoubleClickEvent={onDoubleClick}
      onSelectEvent={onSelectEvent}
      onView={ onViewChange }
      onSelectSlot={onSelectSlot}
      selectable={true}
      view={lastView}
      /* ponemos el evento del componente */
      components={{
        event:CalendarEvent,
      }}
      /* style={{ height: 500 }} */
    />

    <AddNewFab />
    {activeEvent &&<DeletedEventFab /> }

    <CalendarModal />
    </div>
  )
}
