import React, { useEffect, useState } from 'react';
import Modal from 'react-modal';
import DateTimePicker from 'react-datetime-picker';
import moment from 'moment';
import Swal from 'sweetalert2';
import {useDispatch, useSelector} from 'react-redux';
import { uiCloseModal } from '../../actions/ui';
import { eventClearActiveEvent, eventStartAddNew, eventStartUpdate } from '../../actions/event';




const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
  };
  Modal.setAppElement('#root');

const now= moment().minutes(0).seconds(0).add(1,'hours'); //le agrega una hora al momento actual
const nowPlus1=now.clone().add(1, 'hours'); //agregar una hora

const initEvent={
    title:'',
    notes:'',
    start: now.toDate(),
    end: nowPlus1.toDate()
}


export const CalendarModal = () => {

const dispatch = useDispatch();
const { activeEvent } = useSelector( state => state.calendar );
const {modalOpen} = useSelector( state => state.ui );
const [dateStart, setDateStart] = useState(now.toDate());
const [dateEnd, setDateEnd] = useState(nowPlus1.toDate());
const [titleValid, setTitleValid] = useState(true)


const [formValues, setFormValues] = useState(initEvent)

const {notes, title, start, end}= formValues;



useEffect(() => {
/* si active event es diferente de null hace esto */
  if(activeEvent){
      setFormValues(activeEvent)

  }else{
      setFormValues(initEvent);
  }
  /* Si active event existe setea Form Values */
  
}, [activeEvent, setFormValues])









const handleInputChange=({target})=>{
    setFormValues({
        ...formValues,
        [target.name]:target.value
    })
}



  /*   const [isOpen, setIsOpen] = useState(true) */

   /*  const closeModal=()=>{
       
        console.log(`cerrar modal`)
        dispatch(uiCloseModal());
        dispatch(eventClearActiveEvent());
        setFormValues(initEvent);

    } */

    const closeModal = () => {
        dispatch( uiCloseModal() );
        setTimeout(() => {
            dispatch( eventClearActiveEvent() );
        }, 200);
        setFormValues( initEvent ); 
    };

    const handleStartDateChange=(e)=>{
        console.log(e) /* recibimos un evento pero trabaja con Date de JS no moment */
        setDateStart(e);
        setFormValues({
            ...formValues,
            start:e,
        })
    }

    const handleEndDateChange=(e)=>{
        console.log(e);
        setDateEnd(e);
        setFormValues({
            ...formValues,
            end:e,
        })
    }

    const handleSubmitForm=(e)=>{
        e.preventDefault();
       /*  console.log(formValues); */
        const momentStart=moment(start);
        const momentEnd=moment(end);
        if(momentStart.isSameOrAfter(momentEnd)){
            /* console.log(`Fecha 2 debe ser mayor`); */
            return Swal.fire('Error', 'La fecha fin debe ser mayor a la fecha de inicio', 'error');
            //
        }

        if(title.trim().length <2){
            return setTitleValid(false); //return solito es false
        }

        /* Si el evento es active se edita o actualiza */
        if(activeEvent){
            dispatch(eventStartUpdate(formValues));
        }else{
            /* Crear uno nuevo */

        //todo: realizar grabación de base de datos
      /*   dispatch(eventStartAddNew({
            ...formValues,
            id: new Date().getTime(),
            user:{
                _id:'123',
                name:'Junior'
            }
        })); */
        dispatch(eventStartAddNew(formValues));
        }


        setTitleValid(true);
        closeModal();
    }


  return (
    <Modal
    isOpen={modalOpen}
   /*  onAfterOpen={afterOpenModal} */
    onRequestClose={closeModal}
    style={customStyles}
    /* contentLabel="Example Modal" */
    closeTimeoutMS={200} /* MS Milliseconds */
    className="modal"
    overlayClassName="modal-fondo"
  > 
  <h1> {(activeEvent)?'Editar Evento': 'Nuevo Evento'} </h1>
<hr />
<form className="container"
        onSubmit={handleSubmitForm}
>

    <div className="form-group">
        <label>Fecha y hora inicio</label>
        {/* <input className="form-control" placeholder="Fecha inicio" /> */}
        <DateTimePicker 
        className="form-control"
        onChange={handleStartDateChange} 
        value={  (activeEvent) ? activeEvent.start : dateStart }
        format="y-MM-dd h:mm:ss a"
        amPmAriaLabel="Select AM/PM"
        />
    </div>

    <div className="form-group">
        <label>Fecha y hora fin</label>
       {/*  <input className="form-control" placeholder="Fecha inicio" /> */}
       <DateTimePicker 
        className="form-control"
        onChange={handleEndDateChange} 
   /*      minDate={dateStart} */ /* fecha mínima */
   value={   (activeEvent) ? activeEvent.end : dateEnd } 
        format="y-MM-dd h:mm:ss a"
        amPmAriaLabel="Select AM/PM"
        />
    </div>

    <hr />
    <div className="form-group">
        <label>Titulo y notas</label>
        <input 
            type="text" 
            className={`form-control ${!titleValid &&'is-invalid'}` }
            placeholder="Título del evento"
            name="title"
            autoComplete="off"
            value={title}
            onChange={handleInputChange}
        />
        <small id="emailHelp" className="form-text text-muted">Una descripción corta</small>
    </div>

    <div className="form-group">
        <textarea 
            type="text" 
            className="form-control"
            placeholder="Notas"
            rows="5"
            name="notes"
            value={notes}
            onChange={handleInputChange}
        ></textarea>
        <small id="emailHelp" className="form-text text-muted">Información adicional</small>
    </div>

    <button
        type="submit"
        className="btn btn-outline-primary btn-block"
    >
        <i className="far fa-save"></i>
        <span> Guardar</span>
    </button>

</form>

  
  </Modal>
  )
}
