const baseUrl=process.env.REACT_APP_API_URL;



const fetchSinToken=( endpoint, data, method='GET')=>{
    const url=`${baseUrl}/${endpoint}` //localhost:4000/api/auth o event

    if(method==='GET'){
        return fetch(url);
    }else{ //en caso sea diferente a GET
        return fetch(url,{
            method,
            headers:{
                'Content-type':'application/json'
            },
            body:JSON.stringify(data) //tiene que ser como un string
        })
    }

}

const fetchConToken=( endpoint, data, method='GET')=>{
    const url=`${baseUrl}/${endpoint}` //localhost:4000/api/auth o event
    const token=localStorage.getItem('token') || ''; //si es null devuelve vacío

    if(method==='GET'){
        return fetch(url,{
            method,
            headers:{
                'x-token':token
            }
        });
    }else{ //en caso sea diferente a GET
        return fetch(url,{
            method,
            headers:{
                'Content-type':'application/json',
                'x-token':token
            },
            body:JSON.stringify(data) //tiene que ser como un string
        })
    }

}


export {
    fetchSinToken,
    fetchConToken
}